import React from 'react';
import './Nav.css';

function Nav(props){
  return (
    <>
      <ul className="navMenuList">
        {
        props.navMenu.map((a, i) => {
                return <li className="" key={i}><a href={props.nav[i] } className="navLink"> {a} </a></li>;
            })
            }
      </ul>
    </>
  );
}

export default Nav;